*:focus-visible,
.cx-outline-on-focus-within:has(:focus-visible) {
  outline: 2px auto var(--cx-color-border-accent-1);
  outline-offset: 2px;
}

.cx-visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
